import React from 'react';
import * as FIcon from 'react-feather';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminFactura.css';
import {Page, SDataTable} from "../../components";
import {RUTAS} from "../../constants";
import {RouteComponentProps} from "react-router";

const moment = require('moment-timezone')
const numeral = require("numeral")

export const AdminFactura: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const DataTable = React.createRef<any>()
  const TIPO_FACTURA = history.location.pathname.split('/')[3]

  const visualizar = (row: any) => {
    if (TIPO_FACTURA === 'proveedor') {
      history.push(RUTAS.ADMIN_FACTURA_PROVEEDOR_DETAIL(row.id))
    } else {
      history.push(RUTAS.ADMIN_FACTURA_CLIENTE_DETAIL(row.id))
    }

  }

  const renderAcciones = (row: any) => {
    return <div>
      <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}><FIcon.Eye size={14}/></span>
    </div>
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Facturas {TIPO_FACTURA}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Facturas {TIPO_FACTURA}</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  let cliente = history.location.pathname.split('/')[3] === 'cliente'
                  return rootStore.environment.api.facturas(page, perPage, order, search, cliente)
                }}
                columns={[
                  {
                    selector: 'id',
                    omit: true,
                    cell: (row: any) => row.id
                  },
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: false,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                  },
                  {
                    name: 'N. factura',
                    selector: 'descripcion',
                    sortable: false,
                    cell: (row: any) => row.descripcion,
                  },
                  {
                    name: 'Razn social',
                    selector: 'nombre',
                    sortable: false,
                    cell: (row: any) => row.nombre,
                  },
                  {
                    name: 'Total',
                    selector: 'monto',
                    sortable: false,
                    cell: (row: any) => {
                      return 'Gs. ' + numeral(row.monto).format('0,0')
                    },
                  },
                  {
                    name: 'Saldo',
                    selector: 'pendiente',
                    sortable: false,
                    omit: TIPO_FACTURA === 'cliente',
                    cell: (row: any) => {
                      return 'Gs. ' + numeral(row.pendiente).format('0,0')
                    },
                  },
                  {
                    name: 'Cancelada',
                    selector: 'pendiente',
                    sortable: false,
                    omit: TIPO_FACTURA === 'cliente',
                    cell: (row: any) => row.pendiente > 0 ? 'No' : 'Si',
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminFactura;
